import React from 'react'
import { Link } from 'gatsby'
import { Button } from 'theme-ui'
import { Layout, Stack, Main } from '@layout'
import PageTitle from '@components/PageTitle'
import Section from '@components/Section'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
// Michael Lu, 2024/10/6
import { graphql } from 'gatsby'

export const query = graphql`
  query MobileMenuQuery {
    allArticleCategory(
      filter: { hideCat: { ne: "true" } }
      sort: { catOrder: ASC }
    ) {
      nodes {
        catOrder
        hideCat
        name
        slug
      }
    }
    allArticleTag {
      nodes {
        id
        name
        slug
      }
    }
  }
`

const Page404 = props => {
  const catItems = props.data.allArticleCategory.nodes
  const tagItems = props.data.allArticleTag.nodes
  // console.log('PageContact catItems=', catItems, 'tagItems=', tagItems)
  const mobileMenu = []
    .concat(
      catItems && catItems.length > 0
        ? [
            {
              title: '主題',
              items: catItems
            }
          ]
        : []
    )
    .concat(
      tagItems && tagItems.length > 0
        ? [
            {
              title: '標籤',
              items: tagItems
            }
          ]
        : []
    )
  // console.log('PageContact, mobileMenu=', mobileMenu)
  props.pageContext.mobileMenu = mobileMenu

  return (
    <Layout {...props}>
      <Seo title='404 找不到頁面' />
      <Divider />
      <Stack>
        <Main>
          <Section>
            <PageTitle
              header='真抱歉，我們找不到您所要求的頁面。'
              subheader='您的網頁連結可能有誤，或者是那個頁面已下架…'
            />
          </Section>
          <Section>
            <Button variant='primary' as={Link} to='/'>
              回首頁找找看
            </Button>
          </Section>
        </Main>
      </Stack>
    </Layout>
  )
}

export default Page404
